#main {
  position: absolute;
  inset: 0;
  background-color: var(--jp-layout-color0);
}

/* Rise Reveal */
.transition-all {
  transition-property: all;
  transition-duration: 0.2s;
  transition-timing-function: ease;
}

.rise-enabled {
  transition-property: all;
  transition-duration: 0.2s;
  transition-timing-function: ease;

  /* Reveal-specific */

  /* Notebook-specific */

  /* chalkboard plugin and RISE buttons */

  /* button toolbar */

  /* use !important to override stuff that
    would normally be configured through
    chalkboard options */
}

.rise-enabled .reveal table th,
.rise-enabled .reveal table td {
  border-bottom: none;
}

/* do not apply this on the <body> tag */
:not(body).rise-enabled {
  background-color: #fff;
}

.rise-enabled .reveal.fade {
  transition-property: all;
  transition-duration: 0.2s;
  transition-timing-function: ease;
  opacity: 1;
}

.rise-enabled .reveal pre {
  transition-property: all;
  transition-duration: 0.2s;
  transition-timing-function: ease;

  /* font-size: 90%; */
  width: 100%;
  box-shadow: 0 0 0 rgb(0 0 0 / 0%);
  overflow: hidden;
}

.rise-enabled .reveal section img {
  /* transition-property: all;
  transition-duration: 0.2s;
  transition-timing-function: ease; */
  border: 0 solid black;
  box-shadow: 0 0 10px rgb(0 0 0 / 0%);
  transition: none;
}

.rise-enabled .reveal a {
  color: #2980b9;
}

.rise-enabled .reveal a:hover {
  color: #50a0ff;
}

.rise-enabled .reveal .slides {
  width: 100% !important;
  transition-property: all;
  transition-duration: 0.2s;
  transition-timing-function: ease;
  text-align: left;
  font-size: 160%;
}

.rise-enabled .reveal .slide-number {
  transition-property: all;
  transition-duration: 0.2s;
  transition-timing-function: ease;
  right: 3.3em;
  bottom: 3.9em;

  /* more harmful than helpful with reveal 3.8.0
      font-size: 100%;
    */
  color: #727272;
  background-color: transparent;
}

.rise-enabled .reveal .controls .navigate-left,
.rise-enabled .reveal .controls .navigate-left.enabled {
  border-right-color: #727272;
}

.rise-enabled .reveal .controls .navigate-left.enabled:hover,
.rise-enabled .reveal .controls .navigate-left.enabled.enabled:hover {
  border-right-color: #dfdfdf;
}

.rise-enabled .reveal .controls .navigate-right,
.rise-enabled .reveal .controls .navigate-right.enabled {
  border-left-color: #727272;
}

.rise-enabled .reveal .controls .navigate-right.enabled:hover,
.rise-enabled .reveal .controls .navigate-right.enabled.enabled:hover {
  border-left-color: #dfdfdf;
}

.rise-enabled .reveal .controls .navigate-up,
.rise-enabled .reveal .controls .navigate-up.enabled {
  border-bottom-color: #727272;
}

.rise-enabled .reveal .controls .navigate-up.enabled:hover,
.rise-enabled .reveal .controls .navigate-up.enabled.enabled:hover {
  border-bottom-color: #dfdfdf;
}

.rise-enabled .reveal .controls .navigate-down,
.rise-enabled .reveal .controls .navigate-down.enabled {
  border-top-color: #727272;
}

.rise-enabled .reveal .controls .navigate-down.enabled:hover,
.rise-enabled .reveal .controls .navigate-down.enabled.enabled:hover {
  border-top-color: #dfdfdf;
}

.rise-enabled .reveal .progress {
  margin-bottom: 0;
}

.rise-enabled .reveal .progress span {
  background: #727272;
}

.rise-enabled .reveal .reveal-skip,
.rise-enabled .reveal .reveal-notes {
  display: none;
}

/* Rise Reveal Notebook TODO */
.jp-Notebook {
  transition-property: all;
  transition-duration: 0.2s;
  transition-timing-function: ease;
}

.jp-NotebookPanel {
  transition-property: all;
  transition-duration: 0.2s;
  transition-timing-function: ease;
}

.rise-enabled .jp-NotebookPanel {
  overflow: hidden;
}

.rise-enabled .jp-NotebookPanel.reveal {
  padding-top: 0;
}

.rise-enabled .jp-Notebook {
  transition-property: all;
  transition-duration: 0.2s;
  transition-timing-function: ease;

  /* background-color: transparent; */
  box-shadow: none;
  width: 100%;
  height: 100% !important;
}

/* TODO did not find .widget-area in classic */
.rise-enabled .widget-area .form-control,
.rise-enabled .widget-area .dropdown-menu,
.rise-enabled .widget-area .btn {
  font-size: inherit;
}

.rise-enabled .widget-area .dropdown-menu {
  margin: 0;

  /* cancel the settings from reveal */
}

.rise-enabled .jp-Cell li {
  line-height: 160%;
}

.rise-enabled .jp-InputArea pre {
  font-size: 14px;
}

.rise-enabled .jp-InputPrompt {
  font-size: 0.9rem;
  transition-property: all;
  transition-duration: 0.2s;
  transition-timing-function: ease;

  /* font-size: 90%; */
}

.rise-enabled .jp-OutputArea pre {
  font-size: 14px;
}

.rise-enabled .jp-OutputPrompt {
  transition-property: all;
  transition-duration: 0.2s;
  transition-timing-function: ease;
  margin-top: 2px;
}

.rise-enabled .jp-OutputArea-output {
  transition-property: all;
  transition-duration: 0.2s;
  transition-timing-function: ease;
  margin-top: 0.05em;
}

.rise-enabled .jp-MarkdownCell,
.rise-enabled .jp-RawCell {
  font-size: 160%;
}

.rise-enabled .jp-MarkdownCell .jp-InputPrompt,
.rise-enabled .jp-MarkdownCell .jp-OutputPrompt,
.rise-enabled .jp-RawCell .jp-InputPrompt,
.rise-enabled .jp-RawCell .jp-OutputPrompt {
  display: none;
}

.rise-enabled .jp-MarkdownCell.jp-mod-rendered .jp-RenderedHTMLCommon {
  overflow-x: hidden;
  overflow-y: hidden;
}

.rise-enabled .jp-RenderedHTMLCommon h1 {
  font-weight: bold;
}

.rise-enabled .jp-RenderedHTMLCommon h2 {
  font-weight: bold;
}

.rise-enabled .jp-RenderedHTMLCommon h3 {
  font-weight: bold;
}

.rise-enabled .jp-RenderedHTMLCommon h4 {
  font-weight: bold;
  font-style: italic;
}

.rise-enabled .jp-RenderedHTMLCommon h5 {
  font-weight: bold;
  font-style: italic;
}

.rise-enabled .CodeMirror {
  transition-property: all;
  transition-duration: 0.2s;
  transition-timing-function: ease;

  /* font-size: 100%; */
  margin-bottom: 1ex;
}

/* Rise Reveal miscellaneous */
.rise-enabled a.anchor-link {
  display: none;
}

.rise-enabled html {
  overflow-y: auto;
}

.rise-enabled .reveal blockquote p:first-child,
.rise-enabled .reveal blockquote p:last-child {
  display: inherit;
}

/* TODO */
.rise-enabled .jp-Dialog-body kbd {
  padding: 0.1em 0.6em;
  border: 1px solid #ccc;
  font-size: 11px;
  background-color: #f7f7f7;
  color: #333;
  box-shadow:
    0 1px 0 rgb(0 0 0 / 20%),
    0 0 0 2px #fff inset;
  border-radius: 3px;
  display: inline-block;
  margin: 0 0.1em;
  text-shadow: 0 1px 0 #fff;
  line-height: 1.4;
  white-space: nowrap;
}

#header {
  transition-property: all;
  transition-duration: 0.2s;
  transition-timing-function: ease;
  opacity: 1;
  top: 0;
}

.rise-enabled #header {
  transition-property: all;
  transition-duration: 0.2s;
  transition-timing-function: ease;
  top: -200px;
  opacity: 0;
}

/* TODO */
.rise-enabled #site {
  transition-property: all;
  transition-duration: 0.2s;
  transition-timing-function: ease;
  height: 100% !important;
}

/* TODO */
.rise-enabled #ipython-main-app {
  position: static;
}

.rise-enabled ::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

.rise-enabled ::-webkit-scrollbar * {
  background: transparent;
}

.rise-enabled ::-webkit-scrollbar-thumb {
  background: #727272 !important;
}

#notescanvas {
  display: none;
}

.rise-enabled #notescanvas {
  display: block !important;
  position: absolute;
  top: 0;
}

#chalkboard {
  display: none;
}

.rise-enabled #chalkboard {
  display: block !important;
}

.chalkboard-button i {
  display: none;
}

.rise-enabled #help-b,
.rise-enabled .chalkboard-button {
  color: #727272;
  position: fixed !important;
  z-index: 30;
  cursor: pointer;
}

.rise-enabled #help-b {
  left: 0.6em;
  bottom: 0.55em;
}

.rise-enabled .chalkboard-button i {
  display: block !important;
  bottom: 1em !important;
  font-size: 1.8em !important;
  color: #727272 !important;
}

.rise-enabled #toggle-chalkboard.chalkboard-button {
  left: 4em !important;
}

.rise-enabled #toggle-notes {
  left: 7em !important;
}

/* fix for reveal dark themes */
body.rise-enabled.theme-black
  div.jp-RenderedHTMLCommon
  tbody
  tr:nth-child(even),
body.rise-enabled.theme-black div.jp-RenderedHTMLCommon thead th,
body.rise-enabled.theme-black div.jp-InputPrompt,
body.rise-enabled.theme-black div.jp-RenderedText > pre,
body.rise-enabled.theme-black div.jp-RenderedHTMLCommon,
body.rise-enabled.theme-black div.jp-MarkdownCell.jp-mod-rendered {
  color: white;
}

body.rise-enabled.theme-blood
  div.jp-RenderedHTMLCommon
  tbody
  tr:nth-child(even),
body.rise-enabled.theme-blood div.jp-RenderedHTMLCommon thead th,
body.rise-enabled.theme-blood div.jp-InputPrompt,
body.rise-enabled.theme-blood div.jp-RenderedText > pre,
body.rise-enabled.theme-blood div.jp-RenderedHTMLCommon,
body.rise-enabled.theme-blood div.jp-MarkdownCell.jp-mod-rendered,
body.rise-enabled.theme-league
  div.jp-RenderedHTMLCommon
  tbody
  tr:nth-child(even),
body.rise-enabled.theme-league div.jp-RenderedHTMLCommon thead th,
body.rise-enabled.theme-league div.jp-InputPrompt,
body.rise-enabled.theme-league div.jp-RenderedText > pre,
body.rise-enabled.theme-league div.jp-RenderedHTMLCommon,
body.rise-enabled.theme-league div.jp-MarkdownCell.jp-mod-rendered,
body.rise-enabled.theme-night
  div.jp-RenderedHTMLCommon
  tbody
  tr:nth-child(even),
body.rise-enabled.theme-night div.jp-RenderedHTMLCommon thead th,
body.rise-enabled.theme-night div.jp-InputPrompt,
body.rise-enabled.theme-night div.jp-RenderedText > pre,
body.rise-enabled.theme-night div.jp-RenderedHTMLCommon,
body.rise-enabled.theme-night div.jp-MarkdownCell.jp-mod-rendered {
  color: #eee;
}

body.rise-enabled.theme-moon div.jp-RenderedHTMLCommon tbody tr:nth-child(even),
body.rise-enabled.theme-moon div.jp-RenderedHTMLCommon thead th,
body.rise-enabled.theme-moon div.jp-InputPrompt,
body.rise-enabled.theme-moon div.jp-RenderedText > pre,
body.rise-enabled.theme-moon div.jp-RenderedHTMLCommon,
body.rise-enabled.theme-moon div.jp-MarkdownCell.jp-mod-rendered {
  color: #eee8d5;
}
